import React from 'react'
import { Redirect, Route, Switch } from 'react-router-dom'
import {
    // NavbarMenu,
    // MainPage,
    // ContactsPage,
    // FooterComponent,
    // PageContainer,
    // ProductCategoryPage,
    // ProductSubcategoryPage,
    // CartButton,
    // DetailPage,
    // ShoppingCart,
    // PersonalDetails,
    // Wishlist,
    CookiesConsent
} from './components'

import { categoriesData } from './data'
import { useCookies } from "react-cookie";
import { contactsData } from "./data/index"

const NavbarMenu = React.lazy(() => import('./components/NavbarMenu'))
const PageContainer = React.lazy(() => import('./components/PageContainer'))
const FooterComponent = React.lazy(() => import('./components/FooterComponent'))
const CartButton = React.lazy(() => import('./components/CartButton'))
const PersonalDetails = React.lazy(() => import('./components/PersonalDetails/PersonalDetails'))

const MainPage = React.lazy(() => import('./components/Main/MainPage'))
const ContactsPage = React.lazy(() => import('./components/ContactsPage/ContactsPage'))
const DetailPage = React.lazy(() => import('./components/VisualElements/DetailPage'))
const Wishlist = React.lazy(() => import('./components/VisualElements/Wishlist'))
const ShoppingCart = React.lazy(() => import('./components/VisualElements/ShoppingCart'))
const ProductCategoryPage = React.lazy(() => import('./components/VisualElements/ProductCategoryPage'))
const ProductSubcategoryPage = React.lazy(() => import('./components/VisualElements/ProductSubcategoryPage'))


const Routs = () => {
    const [cookies] = useCookies(['user', 'wishlist'])

    return <>
        <React.Suspense fallback={
            <h1 style={{ color: "red", textAlign: "center" }}>LOADING</h1>}>
            <NavbarMenu />
        </React.Suspense>
        <React.Suspense fallback={
            <h1 style={{ color: "red", textAlign: "center" }}>LOADING</h1>}>
            <CartButton />
        </React.Suspense>
        <React.Suspense fallback={
            <h1 style={{ color: "red", textAlign: "center" }}>LOADING</h1>}>
            <CookiesConsent />
        </React.Suspense>
        <React.Suspense fallback={
            <h1 style={{ color: "red", textAlign: "center" }}>LOADING</h1>}>
            <PageContainer>
                <Switch>
                    <Route path="/" exact>
                        <React.Suspense fallback={
                            <h1 style={{ color: "red", textAlign: "center" }}>LOADING</h1>}>
                            <MainPage />
                        </React.Suspense>
                    </Route>
                    <Route path={contactsData.pageRoute} exact>
                        <React.Suspense fallback={
                            <h1 style={{ color: "red", textAlign: "center" }}>LOADING</h1>}>
                            <ContactsPage />
                        </React.Suspense>
                    </Route>
                    {Object.entries(categoriesData.categories).map(([categoryDataKey, categoryDataValue]) =>
                        <Route key={categoryDataValue.route} path={categoryDataValue.route} exact>
                            <React.Suspense fallback={
                                <h1 style={{ color: "red", textAlign: "center" }}>LOADING</h1>}>
                                <ProductCategoryPage categoryKey={categoryDataKey} categoryValue={categoryDataValue} />
                            </React.Suspense>
                        </Route>
                    )}
                    {Object.values(categoriesData.uncategorizedSubcategories).map(subcategoryData =>
                        <Route key={subcategoryData.route} path={subcategoryData.route} exact>
                            <React.Suspense fallback={
                                <h1 style={{ color: "red", textAlign: "center" }}>LOADING</h1>}>
                                <ProductSubcategoryPage subcategory={subcategoryData} />
                            </React.Suspense>
                        </Route>
                    )}
                    {Object.values(categoriesData.categories).map(categoryData =>
                        categoryData.subcategories.map(subcategoryData =>
                            <Route key={subcategoryData.route} path={subcategoryData.route} exact>
                                <React.Suspense fallback={
                                    <h1 style={{ color: "red", textAlign: "center" }}>LOADING</h1>}>
                                    <ProductSubcategoryPage subcategory={subcategoryData} parentFilters={categoryData.filters} />
                                </React.Suspense>
                            </Route>
                        )
                    )}
                    <Route path="/:productId(\d+)" exact>
                        <React.Suspense fallback={
                            <div>
                                <h1 style={{ color: "red" }}>LOADING</h1>
                                <h1 style={{ color: "red" }}>Хопа</h1>
                                <h1 style={{ color: "red" }}>Опа</h1>
                            </div>}>
                            <DetailPage />
                        </React.Suspense>
                    </Route>
                    <Route path="/cart" exact>
                        <React.Suspense fallback={
                            <div>
                                <h1 style={{ color: "red" }}>LOADING</h1>
                                <h1 style={{ color: "red" }}>Хопа</h1>
                                <h1 style={{ color: "red" }}>Опа</h1>
                            </div>}>
                            <ShoppingCart />
                        </React.Suspense>
                    </Route>
                    <Route path="/wishlist" exact>
                        <React.Suspense fallback={
                            <div>
                                <h1 style={{ color: "red" }}>LOADING</h1>
                                <h1 style={{ color: "red" }}>Хопа</h1>
                                <h1 style={{ color: "red" }}>Опа</h1>
                            </div>}>
                            <Wishlist />
                        </React.Suspense>
                    </Route>
                    {cookies.user !== undefined ? <Route path="/login" exact>
                        <React.Suspense fallback={
                            <h1 style={{ color: "red", textAlign: "center" }}>LOADING</h1>}>
                            <PersonalDetails />
                        </React.Suspense>
                    </Route> : <></>}
                    <Redirect to="/" />
                </Switch>
            </PageContainer>
        </React.Suspense>
        <React.Suspense fallback={
            <h1 style={{ color: "red", textAlign: "center" }}>LOADING</h1>}>
            <React.Suspense fallback={
                <h1 style={{ color: "red", textAlign: "center" }}>LOADING</h1>}>
                <FooterComponent />
            </React.Suspense>
        </React.Suspense>
    </>
}

export default Routs
